@import 'utils';
@import 'utils';

.button {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    font-weight: 600;
    font-size: 0.9rem;
    color: white;
    cursor: pointer;
    user-select: none; // Prevent drag
    display: inline-flex;

    // default: Blue button
    &.default {
        background: $oc-blue-6;
        &:hover {
            background: $oc-gray-5;
        }
        &:active {
            background: $oc-blue-6;
        }
    }

    // gray: gray button
    &.gray {
        background: $oc-gray-7;
        &:hover {
            background: $oc-blue-6;
        }
        &:active {
            background: $oc-gray-7;
        }
    }

    // outline: white border button
    &.outline {
        border: 2px solid white;
        border-radius: 2px;
        &:hover {
            background: white;
            color: $oc-blue-6
        }
        &:active {
            background: rgba(255,255,255,0.85);
            border: 2px solid rgba(255,255,255,0.85);
        }
    }

    &:hover {
        @include material-shadow(2,0.5); // Set shadow on the mouse cursor
    }

    // Disabled button
    &.disabled {
        background: $oc-gray-4;
        color: $oc-gray-6;
        cursor: default;
        &:hover, &:active {
            box-shadow: none;
            background: $oc-gray-4;
        }
    }

    // Middle blank on the middle of the button
    & + & {
        margin-left: 0.5rem;
    }
}