@import 'utils';
@import 'utils';

.footer {
  background: $oc-gray-7;
  height: 10rem;
  
  // center
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; // Top-down

  .brand {
    // logo
    color: white;
    font-size: 2rem;
    font-weight: 600;
  }

  .admin-login {
    // Log in 
    margin-top: 0.5rem;
    font-weight: 600;
    font-size: 0.8rem;
    color: rgba(255,255,255,0.8);
    cursor: pointer; // finger cursor
    &:hover { // rid of transparent on hover
      color: white;
    }
  }
}
