@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(//cdn.jsdelivr.net/gh/velopert/font-d2coding@1.2.1/d2coding.css);
.page-template main{background:#f1f3f5;min-height:calc(100vh - 15rem)}

.header{background:#228be6}.header .header-content{height:5rem;width:1400px;margin:0 auto;padding-left:3rem;padding-right:3rem;display:flex;align-items:center}@media (max-width: 1399px){.header .header-content{width:100%}}@media (max-width: 767px){.header .header-content{padding-left:1rem;padding-right:1rem}}.header .header-content .brand{color:white;font-size:1.5rem;font-weight:600}.header .header-content .right{margin-left:auto}

.button{padding-left:0.5rem;padding-right:0.5rem;padding-top:0.25rem;padding-bottom:0.25rem;font-weight:600;font-size:0.9rem;color:white;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:inline-flex}.button.default{background:#228be6}.button.default:hover{background:#adb5bd}.button.default:active{background:#228be6}.button.gray{background:#495057}.button.gray:hover{background:#228be6}.button.gray:active{background:#495057}.button.outline{border:2px solid white;border-radius:2px}.button.outline:hover{background:white;color:#228be6}.button.outline:active{background:rgba(255,255,255,0.85);border:2px solid rgba(255,255,255,0.85)}.button:hover{box-shadow:0 3px 6px rgba(0,0,0,0.08),0 3px 6px rgba(0,0,0,0.115)}.button.disabled{background:#ced4da;color:#868e96;cursor:default}.button.disabled:hover,.button.disabled:active{box-shadow:none;background:#ced4da}.button+.button{margin-left:0.5rem}

.footer{background:#495057;height:10rem;display:flex;align-items:center;justify-content:center;flex-direction:column}.footer .brand{color:white;font-size:2rem;font-weight:600}.footer .admin-login{margin-top:0.5rem;font-weight:600;font-size:0.8rem;color:rgba(255,255,255,0.8);cursor:pointer}.footer .admin-login:hover{color:white}

.list-wrapper{width:1024px;margin:0 auto;padding-top:3rem;padding-bottom:3rem;padding-left:1rem;padding-rigth:1rem}@media (max-width: 1399px){.list-wrapper{width:768px}}@media (max-width: 1023px){.list-wrapper{width:512px}}@media (max-width: 767px){.list-wrapper{width:100%}}

.post-list .post-item{padding:1.5rem;-webkit-transition:all .15s ease-in;transition:all .15s ease-in}.post-list .post-item h2{font-size:2rem;font-weight:400;margin:0;color:#343a40}.post-list .post-item h2 a{-webkit-transition:all .15s ease-in;transition:all .15s ease-in;border-bottom:1px solid transparent}.post-list .post-item h2 a:hover{color:#228be6;border-bottom:1px solid #228be6}.post-list .post-item .date{font-size:0.85rem;color:#adb5bd}.post-list .post-item p{font-weight:300;color:#1c7ed6}.post-list .post-item .tags{font-size:0.85rem;color:#228be6}.post-list .post-item .tags a:hover{color:#339af0;text-decoration:underline}.post-list .post-item .tags a+a{margin-left:0.25rem}.post-list .post-item:hover{background:rgba(34,139,230,0.05)}.post-list .post-item+.post-item{border-top:1px solid #dee2e6}

.pagination{margin-top:2rem;display:flex;align-items:center}.pagination .number{font-size:0.85rem;text-align:center;color:#868e96;flex:1 1}

.post-info{background:#228be6;height:14rem;display:flex;align-items:center;justify-content:center}.post-info .info{margin-top:-5rem;width:1024px;padding:1rem;color:white}.post-info .info h1{font-weight:300;font-size:3rem;margin:0;word-wrap:break-word}.post-info .info .tags{margin-top:1rem;font-size:1.25rem;font-weight:500}.post-info .info .tags a:hover{text-decoration:underline}.post-info .info .tags a+a{margin-left:0.25rem}.post-info .info .date{text-align:right;opacity:0.75;font-style:italic;font-size:1.25rem}@media (max-width: 1023px){.post-info .info{width:768px}.post-info .info h1{font-size:2rem}.post-info .info .tags,.post-info .info .date{font-size:1rem}}@media (max-width: 767px){.post-info{height:auto;padding-bottom:4rem}.post-info .info{padding-top:0;margin:0}.post-info .info .tags{margin-top:0.25rem}.post-info .info .tags,.post-info .info .date{font-size:0.85rem}}

.post-body .paper{padding:2rem;padding-top:2.5rem;padding-bottom:2.5rem;background:white;box-shadow:0 15px 30px rgba(0,0,0,0.125),0 10px 10px rgba(0,0,0,0.11);-webkit-transform:translateY(-3rem);transform:translateY(-3rem);margin:0 auto;min-height:20rem;width:1024px}@media (max-width: 1023px){.post-body .paper{width:768px}}@media (max-width: 767px){.post-body .paper{width:calc(100% -2rem)}}

.markdown-render blockquote{border-left:4px solid #228be6;padding:1rem;background:#f1f3f5;margin-left:0;margin-right:0}.markdown-render blockquote p{margin:0}.markdown-render h1,.markdown-render h2,.markdown-render h3,.markdown-render h4{font-weight:500}.markdown-render h1 code,.markdown-render h2 code,.markdown-render h3 code,.markdown-render h4 code,.markdown-render h5 code,.markdown-render p code{font-family:'D2 Coding';background:#f8f9fa;padding:0.25rem;color:#228be6;border:1px solid #e9ecef;border-radius:2px}.markdown-render code[class*="language-"],.markdown-render pre[class*="language-"]{font-family:'D2 Coding'}.markdown-render a{color:#228be6}.markdown-render a:hover{color:#339af0;text-decoration:underline}.markdown-render table{border-collapse:collapse;width:100%}.markdown-render table,.markdown-render th,.markdown-render td{border:1px solid #ced4da}.markdown-render th,.markdown-render td{font-size:0.9rem;padding:0.25rem;text-align:left}.markdown-render img{max-width:100%;margin:0 auto;display:block}

.question{background:white;padding:2rem}.question .title{font-size:1.25rem;font-weight:500}.question .description{margin-top:0.25rem}.options{padding:1rem;background:#f1f3f5;text-align:right}

@-webkit-keyframes fadesIn{0%{opacity:0}100%{opacity:1}}@keyframes fadesIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@-webkit-keyframes slideUp{0%{-webkit-transform:translateY(50vh);transform:translateY(50vh)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes slideUp{0%{-webkit-transform:translateY(50vh);transform:translateY(50vh)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}@-webkit-keyframes slideDown{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(50vh);transform:translateY(50vh)}}@keyframes slideDown{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(50vh);transform:translateY(50vh)}}.gray-background{background:rgba(100,100,100,0.5);position:fixed;top:0;left:0;right:0;bottom:0;z-index:5}.gray-background.enter{-webkit-animation:fadeIn 0.25s ease-in both;animation:fadeIn 0.25s ease-in both}.gray-background.leave{-webkit-animation:fadeOut 0.25s ease-in both;animation:fadeOut 0.25s ease-in both}.modal-wrapper{z-index:10;position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.modal-wrapper .modal{box-shadow:0 10px 20px rgba(0,0,0,0.095),0 6px 6px rgba(0,0,0,0.115);background:white}.modal-wrapper .modal.enter{-webkit-animation:slideUp 0.25s ease-in both;animation:slideUp 0.25s ease-in both}.modal-wrapper .modal.leave{-webkit-animation:slideDown 0.25s ease-in both;animation:slideDown 0.25s ease-in both}

.editor-template .panes{height:calc(100vh - 4rem);display:flex;background:#f1f3f5}.editor-template .panes .pane{display:flex;min-width:0;overflow:auto}.editor-template .panes .separator{width:1rem;height:100%;position:absolute;-webkit-transform:translate(-50%);transform:translate(-50%);cursor:col-resize}@media (max-width: 767px){.editor-template .panes .editor{flex:1 1 !important}.editor-template .panes .preview,.editor-template .panes .separator{display:none}}

.editor-header{background:#228be6;height:4rem;padding-left:1rem;padding-right:1rem;display:flex;align-items:center}.editor-header .submit{margin-left:auto}

.editor-pane{flex:1 1;display:flex;flex-direction:column}.editor-pane .title{background:#495057;border:none;outline:none;font-size:1.5rem;padding:1rem;color:white;font-weight:500}.editor-pane .title::-webkit-input-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .title::-moz-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .title:-ms-input-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .title::-ms-input-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .title::placeholder{color:rgba(255,255,255,0.75)}.editor-pane .code-editor{flex:1 1;background:#212529;display:flex;flex-direction:column}.editor-pane .code-editor :global .CodeMirror{font-size:1rem;flex:1 1;font-family:'D2 Coding'}.editor-pane .tags{padding-left:1rem;padding-right:1rem;height:2rem;background:#495057;display:flex;align-items:center}.editor-pane .tags .description{font-size:0.85rem;color:white;font-weight:600;margin-right:1rem}.editor-pane .tags input{font-size:0.85rem;border:none;flex:1 1;background:none;outline:none;font-weight:600;color:rgba(255,255,255,0.9)}.editor-pane .tags input::-webkit-input-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .tags input::-moz-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .tags input:-ms-input-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .tags input::-ms-input-placeholder{color:rgba(255,255,255,0.75)}.editor-pane .tags input::placeholder{color:rgba(255,255,255,0.75)}

.preview-pane{flex:1 1;padding:2rem;overflow-y:auto;font-size:1.125rem}.preview-pane .title{font-size:2.5rem;font-weight:300;padding-bottom:2rem;border-bottom:1px solid #ced4da}

.form{background:white;padding:2rem;position:relative;padding-top:2.5rem;width:20rem}.form .close{line-height:2rem;font-size:2rem;position:absolute;right:1rem;top:0.5rem;cursor:pointer}.form .close:hover{color:#868e96}.form .title{font-size:1.25rem;font-weight:500}.form .description{margin-top:0.25rem}.form .error{margin-top:0.5rem;margin-bottom:0.5rem;color:#fa5252;text-align:center;font-size:0.85rem}.form input{width:100%;font-size:1.25rem;margin-top:0.5rem;border:none;border-bottom:1px solid #dee2e6;padding:0.25rem;outline:none;border-radius:4px}.form .login{background:#228be6;text-align:center;color:white;font-weight:500;padding-top:0.5rem;padding-bottom:0.5rem;cursor:pointer;margin-top:1rem;font-size:1.25rem}.form .login:hover{background:#339af0}.form .login:active{background:#228be6}

body{margin:0;box-sizing:border-box;font-family:"Noto Sans KR", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:inherit}a{text-decoration:inherit;color:inherit}

