@import 'utils';
@import 'utils';

.post-body {
    .paper {
        padding: 2rem;
        padding-top: 2.5rem;
        padding-bottom: 2.5rem;
        background: white;
        @include material-shadow(4, 0.5);
        // Make up 3rem to invade the blue background.
        transform: translateY(-3rem);
        margin: 0 auto;
        min-height: 20rem;

        // Different width depends on the resolution
        width: 1024px;
        @include media("<large") { width: 768px; }
        @include media("<medium") { width: calc(100% -2rem); }
    }
}