@import 'utils';
@import 'utils';

.editor-template {
    .panes {
        height: calc(100vh - 4rem); // Minus the EditroHeader from the page height
        display: flex;
        background: $oc-gray-1;
        .pane {
            display: flex;
            min-width: 0;
            overflow: auto;
        }
        .separator {
            width: 1rem;
            height: 100%;
            position: absolute;
            transform: translate(-50%);
            cursor: col-resize;
        }
        @include media("<medium") {
            .editor {
                flex: 1!important;
            }
            .preview, .separator {
                display: none;
            }
        }
    }
}